<template>
  <a-layout class="home" style="height: 100vh">
    <siderLeft :appId="appId"></siderLeft>
    <siderRight></siderRight>
  </a-layout>
</template>

<script>
import siderLeft from './siderLeft'
import siderRight from './siderRight'
import {checkLogin, checkShopId} from '../../common/js/checkLogin'
import {getUrlKey} from '../../common/js/common'
export default {
  name: 'index',
  components: {
    siderLeft,
    siderRight
  },
  data () {
    return {
      appId: ''
    }
  },
  created () {
    this.checkMyLogin()
  },
  mounted () {
  },
  methods: {
    checkMyLogin () {
      // 判断是否登录
      if (!checkLogin()) {
        this.$router.replace({
          name: 'login',
          query: {
          }
        })
      }
      // 判断是否选择店铺
      if (!checkShopId()) {
        this.$router.replace({
          name: 'choiceStore',
          query: {
          }
        })
      }

      this.appId = Number(getUrlKey('appId'))
      // this.appId = this.$route.params.appId
    }
  }
}
</script>

<style lang="stylus" scoped>
</style>



