<template>
  <a-layout-header class="myheader">
    <a-icon
      class="trigger"
      :type="collapsed ? 'menu-unfold' : 'menu-fold'"
      @click="changeCollapsed"
    />
    <span class="myheader-left">欢迎 {{userName}}</span>
<!--    <span class="myheader-right" @click="toCustomerService">客服管理</span>-->
<!--    <span class="myheader-right" @click="showModal">修改密码</span>-->
<!--    <span class="myheader-right" @click="loginMyOut">退出系统</span>-->
<!--    <span class="myheader-right" @click="shopMyOut">退出店铺</span>-->
<!--    <span class="myheader-right" @click="orderContact" v-if="showMessage">消息提醒({{messageNum}})</span>-->
    <a-dropdown>
      <a class="ant-dropdown-link myheader-right">
<!--        <a-avatar :src="user.avatar"/>{{user.name}}-->
        <img src="../../assets/img/logo/user.png">{{user.name}}
<!--        <img :src="user.avatar !== null && user.avatar !== '' ? user.avatar : '../../assets/img/logo/user.png'">{{user.name}}-->
      </a>
      <a-menu slot="overlay">
        <a-menu-item key="0">
          <span  @click="showModal">修改密码</span>
        </a-menu-item>
        <a-menu-divider />
        <a-menu-item key="1">
          <span  @click="loginMyOut">退出系统</span>
        </a-menu-item>
        <a-menu-divider />
        <a-menu-item key="2">
          <span  @click="shopMyOut">退出店铺</span>
        </a-menu-item>
      </a-menu>
    </a-dropdown>
<!--    <a-popover placement="bottom" class="myheader-right">-->
<!--      <template slot="content">-->
<!--       <img :src="downloadPic">-->
<!--      </template>-->
<!--      <template slot="APP下载">-->
<!--        <span>Title</span>-->
<!--      </template>-->
<!--      <a-icon  type="download" style="font-size: 25px;border: 0;width:30px;height:30px;margin-top: 20px;margin-right: 10px" ></a-icon>-->
<!--    </a-popover>-->
<!--    <a-icon title="APP下载" @click="showDownloadModel" class="myheader-right" type="download" style="width:20px;margin-top: 20px;margin-right: 10px" :style="{ fontSize: '25px', color: '#08c' }" />-->
<!--    <a-icon title="客服管理" @click="toCustomerService" class="myheader-right" type="customer-service" style="width:20px;margin-top: 20px;margin-right: 10px" :style="{ fontSize: '25px', color: '#08c' }" />-->
    <a-badge title="客服管理" @click="toCustomerService" class="myheader-right" type="customer-service" style="width:20px;margin-top: 18px;margin-right: 10px" :style="{ fontSize: '25px', color: '#08c' }" ><img src="../../assets/img/logo/contact.png" /></a-badge>
<!--    <a-badge title="消息提示" @click="orderContact" v-if="showMessage" :count="messageNum" class="myheader-right" style="margin-top: 18px;width: 20px;margin-right: 15px"><a-avatar alt="头像" style="opacity: 1" icon="message" /> </a-badge>-->
    <a-badge title="消息提示" @click="orderContact" v-if="showMessage" :count="messageNum" class="myheader-right" style="margin-top: 18px;width: 20px;margin-right: 15px"><img src="../../assets/img/logo/message.png" /></a-badge>
    <a-modal :zIndex="99999" title="修改密码" centered v-model="visible" @ok="handleOk" okText="确认" cancelText="取消">
      <a-form :form="form" layout="vertical">
        <a-form-item label="原始密码" :label-col="{ span: 6 }" :wrapper-col="{ span: 12 }">
          <a-input placeholder="原始密码" v-model="modifyData.password" style="width: 250px"/>
        </a-form-item>
        <a-form-item label="新密码" :label-col="{ span: 6 }" :wrapper-col="{ span: 12 }">
          <a-input placeholder="新密码" v-model="modifyData.newPassword" style="width: 250px"/>
        </a-form-item>
        <a-form-item label="确认新密码" :label-col="{ span: 6 }" :wrapper-col="{ span: 12 }">
          <a-input placeholder="确认新密码" v-model="modifyData.rePassword" style="width: 250px"/>
        </a-form-item>
        <a-form-item v-if="msg">
          <a-alert type="error" :message="msg" banner />
        </a-form-item>
      </a-form>
    </a-modal>
    <a-modal :zIndex="99999" title="到期提醒" centered v-model="visibleOne" @cancel="handleCancel" :footer="false">
      <p style="font-size: 25px;text-align: center">您程序的剩余使用时间不足15天</p>
    </a-modal>
  </a-layout-header>
</template>

<script>
import {loginOut, shopOut} from '../../common/js/checkLogin'
import {getUrlKey} from '../../common/js/common'
export default {
  name: 'myheader',
  data () {
    return {
      collapsed: false,
      visible: false,
      visibleOne: false,
      downloadModel: false,
      form: this.$form.createForm(this, { name: 'coordinated' }),
      modifyData: {
        rePassword: '',
        password: '',
        newPassword: ''
      },
      msg: '',
      userName: localStorage.userId && localStorage.userId !== 'undefined' ? localStorage.userId : '',
      messageNum: 0,
      readStatus: '1',
      permissions: '',
      showMessage: false,
      xs: true,
      downloadPic: process.env.url_api + '/app/version/downloadQRCode',
      appId: '',
      user: {
        name: localStorage.getItem('userName'),
        avatar: localStorage.getItem('avatar')
      }
    }
  },
  // props: {
  //   appId: Number
  // },
  created () {
    this.appId = Number(getUrlKey('appId'))
    this.menuListData(this.appId)
    this.queryByStoreId(localStorage.getItem('shopId'))
  },
  mounted () {
    // setInterval(this.menuListData, 10000)
  },
  methods: {
    async queryByStoreId (value) {
      let res = await this.$Http.queryByStoreId({id: value})
      if (res.code === 0) {
        if (res.sysTenant !== null) {
          this.visibleOne = true
        }
      }
    },
    showDownloadModel () {
      this.downloadModel = true
    },
    handleCancel () {
      this.visibleOne = false
    },
    toCustomerService () {
      window.open('https://mpkf.weixin.qq.com/cgi-bin/kfloginpage', '_blank')
    },
    async menuListData (appId) {
      let res = await this.$Http.navList({
        appId: appId
      })
      if (res.code === 0) {
        this.data = res.menuList
        // this.bus.$emit('goToPage', 'visit') // 默认打开的页面
        this.permissions = res.permissions
        if (this.permissions.indexOf('order:list') !== -1) {
          this.showMessage = true
          this.orderData()
        }
      }
    },
    async orderData () {
      let res = await this.$Http.messageTotal({
        isRead: this.readStatus
      })
      if (res.code === 0) {
        this.messageNum = res.total
      }
    },
    changeCollapsed () {
      this.collapsed = !this.collapsed
      this.bus.$emit('collapsedChange', this.collapsed)
    },
    loginMyOut () {
      // 退出系统
      let that = this
      loginOut(function () {
        that.$router.replace({
          path: '/login',
          query: {
          }
        })
      })
    },
    async modifyPwd (value) {
      let res = await this.$Http.modifyPwd(value)
      if (res.code === 0) {
        this.$message.info('密码修改成功')
        this.visible = res.false
      } else {
        this.msg = res.msg
      }
    },
    showModal () {
      this.visible = true
    },
    handleOk () {
      let password = this.modifyData.password
      if (password === '') {
        this.msg = '原始密码不能为空'
        return
      }
      let newPassword = this.modifyData.newPassword
      if (newPassword === '') {
        this.msg = '新密码不能为空'
        return
      }
      let rePassword = this.modifyData.rePassword
      if (rePassword !== newPassword) {
        this.msg = '确认密码错误'
        return
      }
      this.modifyPwd(this.modifyData)
    },
    shopMyOut () {
      // 退出店铺
      let that = this
      shopOut(function () {
        that.$router.replace({
          path: '/choiceStore',
          query: {
          }
        })
      })
    },
    orderContact () {
      this.bus.$emit('goToPage', 'sysMessage')
    }
  }
}
</script>

<style lang="stylus" scoped>
.myheader
  background #fff
  padding 0
  .trigger:hover
    color #1890ff
  .trigger
    font-size 18px
    line-height 64px
    padding 0 24px
    cursor pointer
    transition color 0.3s
  .myheader-left
    padding 0 10px
    font-weight bold
  .myheader-right:hover
    color #1890ff
  .myheader-right
    float right
    font-weight bold
    padding 0 40px
    padding-left 0px
    cursor pointer
</style>



