<template>
  <a-layout-sider class="siderLeft" v-model="collapsed">
    <div class="logo clear">
      <img class="logo-img" src="../../assets/img/logo/logo2.png">
      <span class="logo-name" v-if="!collapsed">小耳朵</span>
    </div>
    <a-menu theme="dark" v-if="data && data[0] && data[0].list && data[0].list[0]" :defaultSelectedKeys="[data[0].list[0].menuId]" :defaultOpenKeys="[data[0].menuId]" mode="inline">
      <template v-for="item of data">
        <a-sub-menu :key="item.menuId" v-if="item.list && item.list.length && item.list.length > 0">
          <span slot="title"><a-icon :type="item.icon" /><span>{{item.name}}</span></span>
          <a-menu-item v-for="item2 in item.list" :key="item2.menuId" @click="goToPage(item2.url)">{{item2.name}}</a-menu-item>
        </a-sub-menu>
        <a-menu-item :key="item.menuId" @click="goToPage(item.url)" v-else>
          <a-icon :type="item.icon" />
          <span>{{item.name}}</span>
        </a-menu-item>
      </template>
    </a-menu>
  </a-layout-sider>
</template>

<script>
export default {
  name: 'siderLeft',
  props: {
    appId: Number
  },
  data () {
    return {
      data: [],
      collapsed: false
    }
  },
  created () {
    this.menuListData(this.appId)
    this.bus.$on('collapsedChange', collapsed => {
      console.log(collapsed)
      this.collapsed = collapsed
    })
  },
  mounted () {
  },
  watch: {
    appId () {
      this.menuListData(this.appId)
    }
  },
  methods: {
    // 左边菜单接口
    async menuListData (appId) {
      let res = await this.$Http.navList({
        appId: appId
      })
      console.log(res)
      if (res.code === 0) {
        this.data = res.menuList
        // this.bus.$emit('goToPage', 'visit') // 默认打开的页面
        localStorage.permissions = res.permissions
      }
    },
    goToPage (url) {
      this.bus.$emit('goToPage', url)
    }
  }
}
</script>

<style lang="stylus" scoped>
.siderLeft
  overflow-y auto
  height 100vh
  .logo
    height 36px
    vertical-align middle
    // background rgba(255, 255, 255, 0.2)
    margin 16px
    .logo-img
      float left
      width 50px
      height 36px
      margin-left 8px
    .logo-name
      float left
      color #fff
      height 36px
      line-height 36px
      font-weight bold
      font-size 18px
      letter-spacing 4px
      margin-left 14px
</style>



